/*::::::::::::::::::::::::::::::::::::::
Import Tool
::::::::::::::::::::::::::::::::::::::*/
import SmartStringify from "./converter/smartstringify.js";

/*::::::::::::::::::::::::::::::::::::::
Internal Data
::::::::::::::::::::::::::::::::::::::*/
var Support = false;
const CookieConfiguration = {
	"ExpireDuration": 365.25 * 24 * 60 * 60,
	"Domain": location.hostname,
	"Secure": false
};

/*::::::::::::::::::::::::::::::::::::::
General Event Handle
::::::::::::::::::::::::::::::::::::::*/
function EventHandle_NotSupport() {
	console.error("[JavaScript Macro - Cookie] Cookie is not enabled/supported in this browser!");
};
function EventHandle_NoInput(Method) {
	console.error("[JavaScript Macro - Cookie - " + Method + "] No input!");
};
function EventHandle_InvalidArgument(Method, Argument) {
	console.error("[JavaScript Macro - Cookie - " + Method + "] Invalid argument \"" + SmartStringify(Argument) + "\"!");
};
function EventHandle_InvalidKey(Method, Key) {
	console.error("[JavaScript Macro - Cookie - " + Method + "] Invalid key \"" + SmartStringify(Key) + "\"! Key must be type of string!");
};
function EventHandle_InvalidValue(Method, Value) {
	console.error("[JavaScript Macro - Cookie - " + Method + "] Invalid value \"" + SmartStringify(Value) + "\"! Value must be type of string/number/boolean!");
};

/*::::::::::::::::::::::::::::::::::::::
Determine Support Status
::::::::::::::::::::::::::::::::::::::*/
if (navigator.cookieEnabled == true) {
	Support = true;
} else {
	EventHandle_NotSupport();
};

/*::::::::::::::::::::::::::::::::::::::
Secure
::::::::::::::::::::::::::::::::::::::*/
if (location.protocol == "https:") {
	CookieConfiguration["Secure"] = true;
};

/*::::::::::::::::::::::::::::::::::::::
Expire Duration
::::::::::::::::::::::::::::::::::::::*/
function ExpireDuration(Input) {
	if (typeof Input == "undefined") {
		return CookieConfiguration["ExpireDuration"];
	} else if (Number.isSafeInteger(Input) == true && Input > 0) {
		CookieConfiguration["ExpireDuration"] = Input;
	} else {
		console.error("[JavaScript Macro - Cookie] Invalid expire duration \"" + SmartStringify(Input) + "\"! Expire duration must be type of number, safe integer, and larger than 0!");
	};
};

/*::::::::::::::::::::::::::::::::::::::
Internal Function
::::::::::::::::::::::::::::::::::::::*/
function Cookie_Internal(Key, Value, Decay) {
	let ExpireTime;
	if (Decay == true || Decay == "Decay") {
		let OldTime = new Date(0);
		ExpireTime = OldTime.toUTCString();
	} else {
		let CurrentTime = new Date();
		CurrentTime.setTime(CurrentTime.getTime() + (CookieConfiguration["ExpireDuration"] * 1000));
		ExpireTime = CurrentTime.toUTCString();
	};
	let FullCookie = encodeURIComponent(Key) + "=" + encodeURIComponent(Value) + "; expires=" + ExpireTime + "; domain=" + CookieConfiguration["Domain"] + "; path=/";
	if (CookieConfiguration["Secure"] == true) {
		FullCookie += "; secure";
	};
	try {
		document.cookie = FullCookie;
	} catch (Error) {
		console.error("[JavaScript Macro - Cookie] " + Error);
	};
};

/*::::::::::::::::::::::::::::::::::::::
Set
::::::::::::::::::::::::::::::::::::::*/
function Set_Internal(Key, Value) {
	if ((typeof Key == "string" && Key !== "") && (typeof Value == "string" || typeof Value == "number" || typeof Value == "boolean")) {
		Cookie_Internal(Key, Value, false);
	} else {
		if (typeof Key != "string" || Key === "") {
			EventHandle_InvalidKey("Set", Key);
		};
		if (typeof Value != "string" && typeof Value != "number" && typeof Value != "boolean") {
			EventHandle_InvalidValue("Set", Value);
		};
	};
};
function Set() {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	switch (arguments.length) {
		case 0:
			EventHandle_NoInput("Set");
			break;
		case 2:
			Set_Internal(arguments[0], arguments[1]);
			break;
		case 1:
			if (typeof arguments[0] == "object" && arguments[0] !== null) {
				let KeyList = Object.keys(arguments[0]);
				let ValueList = Object.values(arguments[0]);
				for (let index = 0; index < KeyList.length; index++) {
					Set_Internal(KeyList[index], ValueList[index]);
				};
			} else {
				EventHandle_InvalidArgument("Set", arguments[0]);
			};
			break;
		default:
			EventHandle_InvalidArgument("Set", Array.from(arguments));
			break;
	};
};

/*::::::::::::::::::::::::::::::::::::::
Get
::::::::::::::::::::::::::::::::::::::*/
function Get() {
	if (Support != true) {
		EventHandle_NotSupport();
		return undefined;
	};
	let CookieList = document.cookie;
	let CookiePair = {};
	if (CookieList !== "") {
		CookieList = CookieList.split(";");
		for (let index = 0; index < CookieList.length; index++) {
			let CookiePiece = CookieList[index].split("=");
			while (CookiePiece[0].charAt(0) === " ") {
				CookiePiece[0] = CookiePiece[0].substring(1);
			};
			CookiePair[decodeURIComponent(CookiePiece[0])] = decodeURIComponent(CookiePiece[1]);
		};
	};
	switch (arguments.length) {
		case 0:
			return CookiePair;
			break;
		case 1:
			if (typeof arguments[0] == "string" && arguments[0] !== "") {
				return CookiePair[arguments[0]];
			} else if (Array.isArray(arguments[0]) == true) {
				return Get(...arguments[0]);
			} else {
				EventHandle_InvalidKey("Get", arguments[0]);
				return undefined;
			};
			break;
		default:
			let Data = {};
			for (let index = 0; index < arguments.length; index++) {
				if (typeof arguments[index] == "string" && arguments[index] !== "") {
					Data[arguments[index]] = CookiePair[arguments[index]];
				} else {
					EventHandle_InvalidKey("Get", arguments[index]);
				};
			};
			return Data;
			break;
	};
};

/*::::::::::::::::::::::::::::::::::::::
Delete
::::::::::::::::::::::::::::::::::::::*/
function Delete() {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	switch (arguments.length) {
		case 0:
			EventHandle_NoInput("Delete");
			break;
		case 1:
			if (typeof arguments[0] == "string" && arguments[0] !== "") {
				Cookie_Internal(arguments[0], 0, true);
			} else if (Array.isArray(arguments[0]) == true) {
				Delete(...arguments[0]);
			} else {
				EventHandle_InvalidKey("Delete", arguments[0]);
			};
			break;
		default:
			for (let index = 0; index < arguments.length; index++) {
				if (typeof arguments[index] == "string" && arguments[index] !== "") {
					Cookie_Internal(arguments[index], 0, true);
				} else {
					EventHandle_InvalidKey("Delete", arguments[index]);
				};
			};
			break;
	};
};
const Del = Delete;
function DeleteAll() {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	let KeyList = Object.keys(Get());
	for (let index = 0; index < KeyList.length; index++) {
		Cookie_Internal(KeyList[index], 0, true);
	};
};
const DelAll = DeleteAll;

/*::::::::::::::::::::::::::::::::::::::
Rebirth
::::::::::::::::::::::::::::::::::::::*/
function Rebirth() {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	Set(Get());
};

/*::::::::::::::::::::::::::::::::::::::
Package Module
::::::::::::::::::::::::::::::::::::::*/
export {
	Support,
	ExpireDuration,
	Set,
	Get,
	Delete,
	Del,
	DeleteAll,
	DelAll,
	Rebirth
};
