/*::::::::::::::::::::::::::::::::::::::
Time Number
::::::::::::::::::::::::::::::::::::::*/
export default function (CustomConfiguration) {
	const Configuration = {
		"Time": true,
		"Second": false
	};
	if (typeof CustomConfiguration["Time"] == "boolean") {
		Configuration["Time"] = CustomConfiguration["Time"];
	};
	if (typeof CustomConfiguration["Second"] == "boolean") {
		Configuration["Second"] = CustomConfiguration["Second"];
	};
	let Output = "";
	let CurrentTime = new Date();
	let CurrentYear = (CurrentTime.getUTCFullYear()).toString();
	let CurrentMonth = CurrentTime.getUTCMonth() + 1;
	let CurrentDate = CurrentTime.getUTCDate();
	if (CurrentMonth >= 1 && CurrentMonth <= 9) {
		CurrentMonth = "0" + CurrentMonth.toString();
	} else {
		CurrentMonth = CurrentMonth.toString();
	};
	if (CurrentDate >= 1 && CurrentDate <= 9) {
		CurrentDate = "0" + CurrentDate.toString();
	} else {
		CurrentDate = CurrentDate.toString();
	};
	Output = Output + CurrentYear + CurrentMonth + CurrentDate;
	if (Configuration["Time"] == true) {
		let CurrentHour = CurrentTime.getUTCHours();
		let CurrentMinute = CurrentTime.getUTCMinutes();
		if (CurrentHour >= 0 && CurrentHour <= 9) {
			CurrentHour = "0" + CurrentHour.toString();
		} else {
			CurrentHour = CurrentHour.toString();
		};
		if (CurrentMinute >= 0 && CurrentMinute <= 9) {
			CurrentMinute = "0" + CurrentMinute.toString();
		} else {
			CurrentMinute = CurrentMinute.toString();
		};
		Output = Output + CurrentHour + CurrentMinute;
	};
	if (Configuration["Time"] == true && Configuration["Second"] == true) {
		let CurrentSecond = CurrentTime.getUTCSeconds();
		if (CurrentSecond >= 0 && CurrentSecond <= 9) {
			CurrentSecond = "0" + CurrentSecond.toString();
		} else {
			CurrentSecond = CurrentSecond.toString();
		};
		Output = Output + CurrentSecond;
	};
	return Number(Output);
};
