/*::::::::::::::::::::::::::::::::::::::
Virtual Memory Pool
::::::::::::::::::::::::::::::::::::::*/
const Pool = {};

/*::::::::::::::::::::::::::::::::::::::
Import Tool
::::::::::::::::::::::::::::::::::::::*/
import SmartStringify from "./converter/smartstringify.js";

/*::::::::::::::::::::::::::::::::::::::
General Event Handle
::::::::::::::::::::::::::::::::::::::*/
function EventHandle_NoInput(Method) {
	console.error("[JavaScript Macro - Virtual Memory - " + Method + "] No input!");
};
function EventHandle_InvalidArgument(Method, Argument) {
	console.error("[JavaScript Macro - Virtual Memory - " + Method + "] Invalid argument \"" + SmartStringify(Argument) + "\"!");
};
function EventHandle_InvalidKey(Method, Key) {
	console.error("[JavaScript Macro - Virtual Memory - " + Method + "] Invalid key \"" + SmartStringify(Key) + "\"! Key must be type of string!");
};
function EventHandle_InvalidValue(Method, Value) {
	console.error("[JavaScript Macro - Virtual Memory - " + Method + "] Invalid value \"" + SmartStringify(Value) + "\"! Value must not be type of symbol!");
};

/*::::::::::::::::::::::::::::::::::::::
Set
::::::::::::::::::::::::::::::::::::::*/
function Set_Internal(Key, Value) {
	if ((typeof Key == "string" && Key !== "") && typeof Value != "symbol") {
		Pool[Key] = Value;
	} else {
		if (typeof Key != "string" || Key === "") {
			EventHandle_InvalidKey("Set", Key);
		};
		if (typeof Value == "symbol") {
			EventHandle_InvalidValue("Set", Value);
		};
	};
};
function Set() {
	switch (arguments.length) {
		case 0:
			EventHandle_NoInput("Set");
			break;
		case 2:
			Set_Internal(arguments[0], arguments[1]);
			break;
		case 1:
			if (typeof arguments[0] == "object" && arguments[0] !== null) {
				let KeyList = Object.keys(arguments[0]);
				let ValueList = Object.values(arguments[0]);
				for (let index = 0; index < KeyList.length; index++) {
					Set_Internal(KeyList[index], ValueList[index]);
				};
			} else {
				EventHandle_InvalidArgument("Set", arguments[0]);
			};
			break;
		default:
			EventHandle_InvalidArgument("Set", Array.from(arguments));
			break;
	};
};

/*::::::::::::::::::::::::::::::::::::::
Get
::::::::::::::::::::::::::::::::::::::*/
function Get_Internal(Key) {
	return Pool[Key];
};
function Get() {
	switch (arguments.length) {
		case 0:
			return Pool;
			break;
		case 1:
			if (typeof arguments[0] == "string" && arguments[0] !== "") {
				return Get_Internal(arguments[0]);
			} else if (Array.isArray(arguments[0]) == true) {
				return Get(...arguments[0]);
			} else {
				EventHandle_InvalidKey("Get", arguments[0]);
				return undefined;
			};
			break;
		default:
			let Data = {};
			for (let index = 0; index < arguments.length; index++) {
				if (typeof arguments[index] == "string" && arguments[index] !== "") {
					Data[arguments[index]] = Get_Internal(arguments[index]);
				} else {
					EventHandle_InvalidKey("Get", arguments[index]);
				};
			};
			return Data;
			break;
	};
};

/*::::::::::::::::::::::::::::::::::::::
Delete
::::::::::::::::::::::::::::::::::::::*/
function Delete_Internal(Key) {
	delete Pool[Key];
};
function Delete() {
	switch (arguments.length) {
		case 0:
			EventHandle_NoInput("Delete");
			break;
		case 1:
			if (typeof arguments[0] == "string" && arguments[0] !== "") {
				Delete_Internal(arguments[0]);
			} else if (Array.isArray(arguments[0]) == true) {
				Delete(...arguments[0]);
			} else {
				EventHandle_InvalidKey("Delete", arguments[0]);
			};
			break;
		default:
			for (let index = 0; index < arguments.length; index++) {
				if (typeof arguments[index] == "string" && arguments[index] !== "") {
					Delete_Internal(arguments[index]);
				} else {
					EventHandle_InvalidKey("Delete", arguments[index]);
				};
			};
			break;
	};
};
const Del = Delete;
function DeleteAll() {
	let KeyList = Object.keys(Pool);
	for (let index = 0; index < KeyList.length; index++) {
		Delete_Internal(KeyList[index]);
	};
};
const DelAll = DeleteAll;

/*::::::::::::::::::::::::::::::::::::::
Package Module
::::::::::::::::::::::::::::::::::::::*/
export {
	Set,
	Get,
	Delete,
	Del,
	DeleteAll,
	DelAll
};
