/*::::::::::::::::::::::::::::::::::::::
Import Tool
::::::::::::::::::::::::::::::::::::::*/
import SmartStringify from "./converter/smartstringify.js";

/*::::::::::::::::::::::::::::::::::::::
General Event Handle
::::::::::::::::::::::::::::::::::::::*/
function EventHandle_NoInput(Method) {
	console.error("[JavaScript Macro - Document - " + Method + "] No input!");
};
function EventHandle_InvalidArgument(Method, Argument) {
	console.error("[JavaScript Macro - Document - " + Method + "] Invalid argument \"" + SmartStringify(Argument) + "\"!");
};
function EventHandle_InvalidSelector(Method, Selector) {
	console.error("[JavaScript Macro - Document - " + Method + "] Invalid selector \"" + SmartStringify(Selector) + "\"!");
};
function EventHandle_InvalidProperty(Method, Property) {
	console.error("[JavaScript Macro - Document - " + Method + "] Invalid property \"" + SmartStringify(Property) + "\"!");
};
function EventHandle_InvalidPropertyValue(Method, PropertyValue) {
	console.error("[JavaScript Macro - Document - " + Method + "] Invalid property value \"" + SmartStringify(PropertyValue) + "\"!");
};
function EventHandle_SelectorNoMatch(Method, Selector) {
	console.log("[JavaScript Macro - Document - " + Method + "] No match result with selector \"" + SmartStringify(Selector) + "\".");
};

/*::::::::::::::::::::::::::::::::::::::
Style
::::::::::::::::::::::::::::::::::::::*/
function Style_Internal(Selector, Property, PropertyValue) {
	let Invalid = false;
	if (typeof Selector != "string" || Selector === "" || Selector.search("::") != -1 || Selector.search("@") != -1 || Selector.search(":scope") != -1) {
		EventHandle_InvalidSelector("Style", Selector);
		Invalid = true;
	};
	if (Selector.search(":") != -1) {
		//	Event();
		return;
	};
	if (typeof Property != "string" || Property === "") {
		EventHandle_InvalidProperty("Style", Property);
		Invalid = true;
	};
	if (typeof PropertyValue != "string" && typeof PropertyValue != "number" && typeof PropertyValue != "boolean") {
		EventHandle_InvalidPropertyValue("Style", PropertyValue);
		Invalid = true;
	};
	if (Invalid == true) {
		return;
	};
	if (Property.search("-") != -1) {
		if (Property.search("-") == 0) {
			Property = Property.substring(1);
		};
		let PropertyRebuild = Property.split("-");
		for (let index = 1; index < PropertyRebuild.length; index++) {
			PropertyRebuild[index] = PropertyRebuild[index].charAt(0).toUpperCase() + PropertyRebuild[index].substring(1);
		};
		Property = PropertyRebuild.join("");
	};
	let NodeList = document.querySelectorAll(Selector);
	if (NodeList.length == 0) {
		EventHandle_SelectorNoMatch("Style", Selector);
	} else {
		for (let index = 0; index < NodeList.length; index++) {
			try {
				NodeList[index].style[Property] = PropertyValue;
			} catch (Error) {
				console.error("[JavaScript Macro - Document] " + Error);
			};
		};
	};
};
function Style() {
	switch (arguments.length) {
		case 0:
			EventHandle_NoInput("Style");
			break;
		case 3:
			Style_Internal(Selector, Property, PropertyValue);
			break;
		case 2:
			break;
		case 1:
			break;
		default:
			EventHandle_InvalidArgument("Style", Array.from(arguments));
			break;
	};
};

