export default function (CustomConfiguration) {
	const Configuration = {
		"Length": 8,
		"LowerCaseLetter": true,
		"UpperCaseLetter": false,
		"Number": false,
		"Symbol": false
	};
	const Data = {
		"LowerCaseLetter": "abcdefghijklmnopqrstuvwxyz",
		"UpperCaseLetter": "ABCDEFGHIJKLMNOPQRSTUVWXYZ",
		"Number": "0123456789",
		"Symbol": "+-*/~?!@#$%^&*()_=:;,.<>"
	};
	if (CustomConfiguration) {
		if (Number.isSafeInteger(CustomConfiguration["Length"]) && CustomConfiguration["Length"] > 0) {
			Configuration["Length"] = CustomConfiguration["Length"];
		};
		if (typeof CustomConfiguration["LowerCaseLetter"] == "boolean") {
			Configuration["LowerCaseLetter"] = CustomConfiguration["LowerCaseLetter"];
		};
		if (typeof CustomConfiguration["UpperCaseLetter"] == "boolean") {
			Configuration["UpperCaseLetter"] = CustomConfiguration["UpperCaseLetter"];
		};
		if (typeof CustomConfiguration["Number"] == "boolean") {
			Configuration["Number"] = CustomConfiguration["Number"];
		};
		if (typeof CustomConfiguration["Symbol"] == "boolean") {
			Configuration["Symbol"] = CustomConfiguration["Symbol"];
		};
	};
	if (Configuration["LowerCaseLetter"] == false && Configuration["UpperCaseLetter"] == false && Configuration["Number"] == false && Configuration["Symbol"] == false) {
		return undefined;
	};
	let DataChain = "";
	if (Configuration["LowerCaseLetter"] == true) {
		DataChain = DataChain + Data["LowerCaseLetter"];
	};
	if (Configuration["UpperCaseLetter"] == true) {
		DataChain = DataChain + Data["UpperCaseLetter"];
	};
	if (Configuration["Number"] == true) {
		DataChain = DataChain + Data["Number"];
	};
	if (Configuration["Symbol"] == true) {
		DataChain = DataChain + Data["Symbol"];
	};
	let DataChain_Length = DataChain.length;
	let Output = "";
	for (let index = 0; index < Configuration["Length"]; index++) {
		Output = Output + DataChain.charAt(Math.floor(Math.random() * DataChain_Length));
	};
	return Output;
};
